body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: MainFont;
  font-weight: 300;
}

body #dapp {
  width: 100%;
  height: 100%;
  padding: 0;
  position: relative;
}

body #dapp .overlay {
  background: linear-gradient(-35deg, var(--overlayA) 0%, var(--overlayB) 100%);
  z-index: 999;
  pointer-events: none;
  position: absolute;
  inset: 0;
}

body #dapp .backdrop {
  z-index: 0;
  box-shadow: cardDrop, 0px 0px 2px outerspace03;
  pointer-events: none;
  border-radius: 6px;
  position: absolute;
  inset: 0;
}

body #dapp .splash {
  z-index: 0;
  position: absolute;
  inset: 0;
}

body #dapp .mainLeft {
  -webkit-user-select: none;
  width: 72px;
  padding-left: 2px;
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

body #dapp .mainLeft .accountTile {
  color: var(--outerspace);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 6px 0;
  display: flex;
  position: absolute;
  top: 26px;
  left: 5px;
  right: 5px;
}

body #dapp .mainLeft .accountTile .accountIcon {
  background: var(--ghostA);
  cursor: pointer;
  border-radius: 16px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 58px;
  height: 58px;
  display: flex;
}

body #dapp .mainLeft .accountTile .accountIcon:hover {
  background: var(--ghostB);
}

body #dapp .mainLeft .dappIcons {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 72px 0;
}

body #dapp .mainLeft .dappIcons .dappIconsScroll {
  -webkit-user-select: auto;
  width: 100%;
  max-height: 100%;
  overflow: hidden scroll;
}

body #dapp .mainLeft .dappIcons .dappIconsScroll .dappIconsWrap {
  padding-top: 10px;
}

body #dapp .main {
  color: var(--outerspace);
  -webkit-user-select: none;
  z-index: 9999;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 0;
}

body #dapp .main .mainDappBackground {
  -webkit-user-select: none;
  z-index: 1;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 0;
}

body #dapp .main .mainDappLoading .mainDappLoadingText {
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1px;
  font-family: MainFont;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}

body #dapp .main .mainDappLoading .loader, body #dapp .main .mainDappLoading .loader:after {
  border-radius: 100%;
  width: 128px;
  height: 128px;
}

body #dapp .main .mainDappLoading .loader {
  text-indent: -9999em;
  border-top: 3px solid var(--ghostY);
  border-right: 3px solid var(--ghostY);
  border-bottom: 3px solid var(--ghostY);
  border-left: 3px solid var(--ghostD);
  will-change: transform;
  font-size: 7px;
  position: relative;
  transform: translateZ(0);
  animation: .48s linear infinite load8 !important;
}

@keyframes load8 {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

body #dapp .main .mainDappEnableChains {
  background: var(--ghostC);
  border-bottom: 2px solid var(--ghostZ);
  height: 40px;
  box-shadow: 0px 1px 4px var(--ghostX);
  box-sizing: border-box;
  cursor: pointer;
  letter-spacing: 1px;
  text-transform: uppercase;
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  padding: 1px 20px 0 21px;
  font-size: 14px;
  font-weight: 400;
  display: flex;
}

body #dapp .main .mainDappEnableChains:hover {
  background: var(--ghostD);
  color: var(--outerspace);
}

body #dapp .main .mainApps {
  background: var(--ghostY);
  border-radius: 16px;
  width: 600px;
  height: 300px;
}

body #dapp .top {
  z-index: 50;
  height: 37px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

body #dapp .top .break {
  opacity: .2;
  background: #fff;
  height: 1px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

body #dapp .top .title {
  z-index: 1;
  letter-spacing: 1px;
  pointer-events: none;
  user-select: none;
  justify-content: center;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Ubuntu, Cantarell, Oxygen Sans, Helvetica Neue, sans-serif;
  font-size: 12px;
  font-weight: 300;
  display: flex;
  position: absolute;
  inset: 0;
}

body #dapp .top .menu {
  z-index: 3;
  width: 85px;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
}

body #dapp .top .menu .reload {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 37px;
  padding-top: 1px;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
}

body #dapp .shade {
  transition: var(--standard);
  z-index: 150;
  border-bottom: 2px solid outerspace05;
  box-shadow: reverseCardDrop;
  pointer-events: none;
  border-radius: 6px;
  position: absolute;
  inset: 37px 0 0;
}

body #dapp .webloading {
  z-index: 100;
  transition: var(--standard);
  background: linear-gradient(180deg, highlight 0%, thick 100%);
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 37px 0 0;
}

body #dapp .webloading img {
  max-width: 40px;
  max-height: 40px;
  position: absolute;
}

body #dapp .webwrap {
  z-index: 1000;
  transition: var(--standard);
  border-radius: 6px;
  position: absolute;
  inset: 37px 0 0;
}

body #dapp .webwrap .view {
  z-index: 100;
  border-radius: 6px;
  position: absolute;
  inset: 0;
  overflow: hidden;
}

body #dapp .webwrap view {
  width: 100%;
  height: 100%;
}

.dappTile {
  color: var(--outerspace);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 6px 0;
  display: flex;
}

.dappTile .dappIcon {
  background: var(--ghostA);
  cursor: pointer;
  border-radius: 16px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 58px;
  height: 58px;
  display: flex;
}

.dappTile .dappIcon:hover {
  background: var(--ghostB);
}

.dappTile .dappName {
  text-transform: uppercase;
  font-size: 10px;
  font-weight: 400;
}

@keyframes load {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
